@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  background: url("https://images.unsplash.com/photo-1518022525094-218670c9b745?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=687&q=80");
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
  height: 100vh;
  font-family: "Poppins", sans-serif;
  color: white;
}
.container {
  padding: 30px 50px;
}
.timezone {
  position: absolute;
  top: 2%;
  left: 2%;
}
.timezone #time {
  font-size: 70px;
  text-align: center;
}
.timezone #ampm {
  font-size: 30px;
  margin-left: -15px;
}
.timezone #date {
  font-size: 30px;
  margin-top: -20px;
}

.search {
  position: absolute;
  width: 240px;
  top: 5%;
  right: 5%;
  padding: 5px;
  color: white;
  background-color: white;
  border-radius: 25px;
}
.search input[type="text"] {
  padding: 10px;
  border: 1px solid white;
  outline: none;
  border-radius: 25px;
}
.search button {
  padding: 10px;
  border: 1px solid black;
  border-radius: 50%;
  background-color: rgb(31, 29, 29);
  color: white;
}

#place {
  display: flex;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 6vw;
  font-weight: 800;
  letter-spacing: 5px;
  color: white;
}
.temp span {
  font-size: 2vw;
  margin-left: -20px;
  font-weight: 1000;
}
#weatherName {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3vw;
  font-weight: 500;
  color: white;
}
.weatherInfo {
  width: 100%;
  position: fixed;
  display: flex;
  justify-content: center;
  bottom: 0;
  background-color: rgba(10, 10, 10, 0.5);
}
.weatherItems {
  border-radius: 5%;
  border: 1px solid white;
  padding: 10px 50px;
  margin: 30px;
  font-size: 25px;
  font-weight: 800;
  align-items: center;
  display: flex;
  text-align: center;
  flex-flow: wrap column;
  justify-content: space-around;
}
.weatherItems img {
  width: 100px;
  padding: 20px;
}
.weatherItems div {
  text-decoration: underline;
  text-underline-position: under;
  text-decoration-color: red;
}
.weatherItems span {
  font-size: 20px;
}

/* RESPONSIVE WEB SCREEEN */

@media only screen and (max-width: 730px) {
  .container {
    padding: 15px 25px;
  }
  .timezone {
    left: 7%;
    top: 3%;
  }
  .search {
    top: 32%;
    width: 230px;
    left: 48%;
    transform: translate(-50%, -50%);
    padding: 5px;
  }
  .timezone #time {
    font-size: 50px;
  }
  .timezone #ampm {
    font-size: 18px;
    margin-left: -13px;
  }
  .timezone #date {
    font-size: 20px;
  }
  #place {
    top: 48%;
    font-size: 8vw;
    font-weight: 800;
    letter-spacing: 2px;
  }
  #weatherName {
    top: 55%;
    font-size: 7vw;
    font-weight: 500;
    color: white;
  }
  .search input[type="text"] {
    width: 180px;
  }
  .temp span {
    margin-left: -2px;
    font-size: 4vw;
  }
  .weatherInfo {
    justify-content: start;
    align-items: none;
    overflow-y: scroll;
  }
  .weatherItems {
    padding: 15px 35px;
    margin: 10px;
    font-size: 20px;
  }
  .weatherItems div {
    font-size: 18px;
    white-space: nowrap;
  }
  .weatherItems span {
    font-size: 18px;
    white-space: nowrap;
  }
  .weatherItems img {
    margin: -5px;
    width: 100px;
  }
}
